const burgerButton = document.querySelector('.burger-button');
const navigation = document.querySelector('nav');
const expandVideo = document.querySelector(".expand");
const closeVideo = document.querySelector(".close-expanded-video");
const bars = [...document.querySelectorAll(".bar")];
const body = document.querySelector('body');
const videoFullScreenControls  = [ expandVideo, closeVideo ];
const movieTitles = document.querySelector('.video-text-scroll');
movieTitlesScroll(movieTitles, 3000);

burgerButton.addEventListener("mousedown", evt => {
  burgerButton.classList.toggle('is-open');
  navigation.classList.toggle('is-open');
  body.classList.toggle('fullscreen');
  if ( burgerButton.classList.contains("is-open")){
    window.scrollTo(0,0);
    disableScroll();
  } else {
    enableScroll();
  }
})

videoFullScreenControls.forEach( control => control.addEventListener("mousedown", evt => {
  bars.forEach( b => b.classList.toggle('hide'));
  body.classList.toggle('fullscreen');
  if ( bars[0].classList.contains("hide")){
    window.scrollTo(0,0);
    disableScroll();
  } else {
    enableScroll();
  }
}));


const parallax = new Rellax('.parallax');

