import animate from "animateplus";
import { Linear } from "gsap/EasePack";

const movieTitlesScroll = async (elem, delay) => {
  const height = elem.offsetHeight;
  const parentHeight = elem.parentElement.offsetHeight;
  const ratio = parentHeight / height;
  await animate({
    elements: elem,
    duration: 12000,
    easing: "linear",
    // delay: 3000,
    loop: true,
    transform: ["translateY(100%)", `translateY(${(1- ratio)*100 - 100}%)`]
  }).then( () => { 
    new Promise(function(res, rej){
      setTimeout( () => { res(); }, delay);
    })
  }
)

  // await new Promise(function(res, rej){
  //   setTimeout(()=>{
  //     res();
  //   }, delay);
  // })

  // await movieTitlesScroll(elem, delay);
};

export default movieTitlesScroll;